@font-face {
  font-family: 'PT Sans';
  src: url("/fonts/Pt%20sans%20reg.ttf");
}
@font-face {
  font-family: 'RobotoCondensed';
  src: url("/fonts/RobotoCondensedRegular.ttf");
}
@font-face {
  font-family: 'PTSans';
  src: url("/fonts/pt-sans.bold.ttf");
}
body {
  font-size: 16px;
  /* global font */
}
.block-center .position-portfolio .bot-block {
  padding: 1% 5% 1% 5%;
}
.block-center .position-portfolio .bot-block p {
  margin: 2% 1% 0 1%;
}
.block-center .position-portfolio .bot-block h1 {
  padding-bottom: 20px;
}
.navbar-inverse .navbar-toggle {
  border-color: #0e5656;
  background-color: #126c6c;
}
.navbar-inverse .navbar-toggle .icon-bar {
  background-color: #24d9da;
}
.navbar-inverse .navbar-toggle:hover,
.navbar-inverse .navbar-toggle:focus {
  background-color: #158282;
}
.navbar-inverse .navbar-collapse {
  border: none;
  box-shadow: none;
}
@media all and (min-width: 320px) and (max-width: 780px) {
  .navbar-inverse .navbar-collapse {
    background-color: #4b98ae;
  }
}
.navbar-inverse.navbarCustom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
  border: none;
  border-radius: 0;
  background: -webkit-linear-gradient(left, rgba(107, 171, 211, 0.3), rgba(34, 194, 196, 0.3));
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(right, rgba(107, 171, 211, 0.3), rgba(34, 194, 196, 0.3));
  /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(right, rgba(107, 171, 211, 0.3), rgba(34, 194, 196, 0.3));
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(to right, rgba(107, 171, 211, 0.3), rgba(34, 194, 196, 0.3));
  /* Standard syntax */
}
.navbar-inverse.navbarCustom li {
  list-style: none;
}
.navbar-inverse.navbarCustom li:hover {
  background-color: #158282;
}
.navbar-inverse.navbarCustom li a {
  padding: 5px 20px;
  line-height: 50px;
  text-decoration: none;
  font-size: 16px;
  color: #d3e1e7;
}
.navbar-inverse.navbarCustom li a:hover {
  color: white;
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100;
  height: 100%;
  overflow: auto;
  background-color: #000000;
  background-color: rgba(0, 0, 0, 0.9);
}
.modal-content {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
}
#caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}
.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}
@media only screen and (max-width: 700px) {
  .modal-content {
    width: 100%;
  }
}
.portfolio {
  background-image: url('/img/hiw-bg.png');
  height: 100%;
}
.portfolio h1 {
  margin: 10% 0 0 6%;
  font-family: 'RobotoCondensed';
  color: #ffffff;
  font-size: 36px;
  font-weight: 700;
  line-height: 35px;
}
.first-bg {
  position: absolute;
  height: 100%;
  width: 100%;
}
.main-screen-bg {
  position: absolute;
  width: 100%;
  height: 100%;
}
.block-center {
  padding: 5% 6% 0 6%;
}
.block-center .gallery-block {
  padding-top: 6%;
  margin-top: 6%;
  margin-bottom: 4%;
}
.block-center .gallery-block .images img {
  width: 70%;
  margin: auto;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 3px;
}
.block-center .gallery-block .images img:hover {
  opacity: 0.7;
}
.block-center .position-portfolio {
  margin-top: 3%;
  margin-bottom: 4%;
}
.block-center .position-portfolio a:hover {
  opacity: 0.75;
  text-decoration: none;
}
.block-center .position-portfolio .top-block img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.block-center .position-portfolio .bot-block {
  height: 30%;
  padding-bottom: 20px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #ffffff;
}
.block-center .position-portfolio .bot-block h1 {
  font-family: RobotoCondensed;
  color: #22282b;
  line-height: 30px;
  font-size: 24px;
  font-weight: 700;
  text-decoration: underline;
  margin: 0 5% 0 5%;
  padding-top: 30px;
  text-align: left;
}
.block-center .position-portfolio .bot-block h2 {
  font-family: RobotoCondensed;
  color: #22282b;
  line-height: 30px;
  font-size: 24px;
  font-weight: 700;
  margin: 0 5% 0 5%;
  padding-top: 30px;
  text-align: left;
}
.block-center .position-portfolio .bot-block p {
  font-family: 'PT Sans';
  color: #22282b;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  margin: 5%;
}
